.App-header {
	height: 15vh;
	width: 100%;
	color: white;
	background-color: #222;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-direction: column;
	    flex-direction: column;
	-ms-flex-pack: justify;
	    justify-content: space-between;
}

.header-section {
	-ms-flex: 1 1;
	    flex: 1 1; 
	display: -ms-flexbox; 
	display: flex;
	-ms-flex-direction: column;
	    flex-direction: column;
	-ms-flex-pack: center;
	    justify-content: center;
}

.App-title {
	font-size: 5vh;
	margin: 0;
}

.station-row {
	overflow-x: auto;
	white-space: nowrap;
}

.station-name-header {
	margin: 0;
	font-size: 4.3vh;
}

.station-dropdown {
	height: 4vh;
}


